.testimonials {
  &__title {
    display: block;
    font-size: 13px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #c0cdd7;
    padding-left: 220px;
    margin-bottom: 16px;
  }

  &__heading {
    max-width: 420px;
    font-weight: 600;
    font-size: 32px;
    color: $heading-text-color;
    padding-left: 220px;
    margin-bottom: 85px;
  }

  &__person-container {
    display: flex;
    justify-content: center;
    margin: 0 auto 150px;
  }
}

@media (max-width: $small-screen-breakpoint) {
  .testimonials__person-container {
    flex-direction: column;
    align-items: center;
  }

  .testimonials__heading,
  .testimonials__title {
    margin: 0 auto 30px;
    padding-left: 0;
    text-align: center;
  }
}
